.icon-left{
  position: absolute;
  top: 10px;
  left: 10px;
}

.icon-right{
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.season-display{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.season-display.winter i{
  color: #9ccbf3;
}

.season-display.summer i{
  color: red;
}

.winter{
  background-color: #d3e4f3;
}

.summer{
  background-color: orange;
}
